.input-field{
    margin: 10px 15px;
    border-style: none;
    border-color: white;
    background-color: white;
    border-color: grey;
    line-height: 3rem;
    width: 70%;
    color: rgb(75, 69, 69)(141, 130, 130);
    padding: 0 2px;
    font-weight: 700;
    font-size : 1rem;
    height: 50px;
    border-bottom: solid rgb(236, 233, 222);
}

.input-field:focus{
    outline: none;
    border-bottom: solid rgb(233, 206, 119);
}

.action-button{
    margin-top: 20px;
    background-color: rgb(233, 206, 119);
    color: white;
    width: 50%;
    height: 50px;
    font-size: 1rem;
    font-weight: 700;
    line-height: 2rem;
    border-radius : 5px;
    border-style: none;
    transition: 0.3s;
}

.action-button:hover{
    background-color: rgb(255, 255, 255);
    color: rgb(25, 91, 129);
    cursor: pointer;
}