.landing-container-bckg{

    background-image: url('../../img/wallpaperflare.com_wallpaper.jpg');
    width: 100vw;
    height : 100vh;
    text-align: center;
    position:fixed
}

.landing-container-main{
    height: 70%;
    width: 60%;
    position : relative;
    top: 15%;
    left: 20%;
    display:  flex;
    box-shadow: 0 0 5px rgb(141, 179, 177);
}


.landing-container-left{
    background-image: linear-gradient(rgb(101, 201, 201),rgb(56, 130, 180));
    background-size: 100% 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white
}
.landing-container-right{
    background-color: white;
    width: 50%;
}

.landing-container-right-formswitch{
    margin: 10px 10px 0 0 ;
    display: flex;
    justify-content: flex-end;
}
.formswitch:first-of-type{
    border-top-left-radius:5px;
    border-bottom-left-radius:5px;
    
    border-right: none;
    
}
.formswitch:last-of-type{
    border-top-right-radius:5px;
    border-bottom-right-radius:5px;
    background-color: white;
    border-left: none
}
.formswitch{
    border: hidden rgb(241, 235, 235);
    border-width: 1px;
    padding: 3px 5px;
    width: 20%;
    height: 30px;
    box-shadow: 0 0 2px rgb(192, 233, 240);
    font-weight: 800;
    transition : 0.5s;
}
.formswitch:focus{
    outline: none;    
}

.formswitch:hover{
    cursor: pointer;
}